import { memo, useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import BentoCard from './BentoCard';

const ProfileWidget = memo(function ProfileWidget({ className = "" }) {
  const titles = [
    "Front End Web Developer",
    "Graphic Designer",
    "Hobbyist"
  ];
  const [currentTitleIndex, setCurrentTitleIndex] = useState(0);
  const [isFlipped, setIsFlipped] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkDevice = () => {
      setIsMobile(window.innerWidth <= 768);
    };
    
    // Initial check
    checkDevice();
    
    // Add resize listener
    window.addEventListener('resize', checkDevice);

    const interval = setInterval(() => {
      setCurrentTitleIndex((prev) => (prev + 1) % titles.length);
    }, 3000); // Change title every 3 seconds

    return () => {
      clearInterval(interval);
      window.removeEventListener('resize', checkDevice);
    };
  }, []);

  const handleFlip = () => {
    if (isMobile) {
      setIsFlipped(!isFlipped);
    }
  };

  return (
    <BentoCard className={`bg-gradient-to-br from-gray-800/30 to-gray-700/30 ${className}`}>
      <div className="flex flex-col items-center justify-center h-full text-center">
        <div 
          className="relative w-24 h-24 mb-4 [perspective:1000px] cursor-pointer"
          onClick={handleFlip}
        >
          <motion.div
            className="relative w-full h-full [transform-style:preserve-3d]"
            animate={{ rotateY: isFlipped ? 180 : 0 }}
            transition={{ duration: 0.6, type: "spring", stiffness: 100 }}
            onHoverStart={() => !isMobile && setIsFlipped(true)}
            onHoverEnd={() => !isMobile && setIsFlipped(false)}
          >
            {/* Front side */}
            <div className="absolute inset-0 [backface-visibility:hidden]">
              <img
                src="/images/profile.jpg"
                alt="Profile"
                className="w-full h-full object-cover rounded-3xl border-2 border-gray-400/30"
                loading="lazy"
              />
            </div>
            {/* Back side */}
            <div className="absolute inset-0 [backface-visibility:hidden] [transform:rotateY(180deg)]">
              <img
                src="/images/logo.jpg"
                alt="Logo"
                className="w-full h-full object-cover rounded-3xl border-2 border-gray-400/30"
                loading="lazy"
              />
            </div>
          </motion.div>
        </div>
        <h2 className="text-xl font-bold text-gray-200 mb-2">Muhammad Farrel Rabbani</h2>
        <div className="h-8 w-full relative">
          <AnimatePresence mode="wait">
            <motion.div
              key={currentTitleIndex}
              initial={{ y: 20, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              exit={{ y: -20, opacity: 0 }}
              transition={{ 
                duration: 0.5,
                ease: "easeInOut"
              }}
              className="absolute inset-0 flex items-center justify-center"
            >
              <span className="text-sm text-gray-300 font-medium">
                {titles[currentTitleIndex]}
              </span>
            </motion.div>
          </AnimatePresence>
        </div>
      </div>
    </BentoCard>
  );
});

export default ProfileWidget; 